.App {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  background-color: whitesmoke;
}
.App__Panel {
  width: 70vw;
  height: 50vh;
  margin-left: 15vw;
  transform: translateY(-30vh);
  background-color: white;
  border-radius: 2vh;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}
@media screen and (orientation: portrait) {
  .App__Panel {
    width: 90vw;
    margin-left: 5vw;
  }
}
.App__Panel__Course {
  padding-left: 4vw;
  padding-bottom: 2vh;
  height: auto;
  height: 1vh;
}
.App__Panel__Course__text {
  font-size: 3.5vh;
  animation: textAppear 400ms;
  position: sticky;
}
@media screen and (orientation: portrait) {
  .App__Panel__Course__text {
    font-size: 2.5vh;
  }
}
@keyframes textAppear {
  from {
    opacity: 0;
    transform: translateY(5vh);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.App__Panel__Bottom {
  width: 100%;
  height: auto;
  transition: 250ms;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 4vw;
  padding-right: 4vw;
}
.App__Panel__Form {
  width: 100%;
  height: 25%;
  display: flex;
  justify-content: space-between;
  padding-left: 4vw;
  padding-right: 4vw;
  align-items: center;
  transition: 250ms;
}
.App__Panel__Form__Element {
  width: auto;
  height: auto;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}
.App__Panel__Form__Element__label {
  font-size: 2vh;
  font-weight: 600;
}
.App__Panel__Form__Element__input {
  margin-top: 0.5vh;
  width: 19vw;
  height: 6vh;
  padding-left: 1vh;
  font-size: 1.75vh;
  border-radius: 1vh;
  border: none;
  outline: none;
  box-shadow: rgba(9, 30, 66, 0.25) 0px 1px 1px, rgba(9, 30, 66, 0.13) 0px 0px 1px 1px;
  overflow: hidden;
}
@media screen and (orientation: portrait) {
  .App__Panel__Form__Element__input {
    width: 24vw;
  }
}
.App__Panel__Form__Element__input::-webkit-inner-spin-button, .App__Panel__Form__Element__input::-webkit-outer-spin-button {
  display: none;
}
.App__banner {
  width: 100vw;
  height: 50vh;
  background-color: #37245c;
}
.App__banner__title {
  text-align: center;
  font-size: 5vh;
  color: white;
  padding-top: 3vh;
}
.App__banner__subtitle {
  text-align: center;
  font-size: 2.25vh;
  color: white;
  padding-top: 1vh;
  font-weight: 300;
}