.Notification {
  width: 35vh;
  height: 6vh;
  background-color: rgb(235, 235, 235);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 1vw;
  padding-right: 1vw;
  border-radius: 1vh;
}
.Notification__image {
  height: 60%;
  opacity: 0.5;
}
.Notification__text {
  max-height: 100%;
  max-width: 85%;
  font-size: 1.05vh;
}