.RatesTable {
  width: 50vw;
  height: auto;
  margin-left: 25vw;
  margin-top: 5vh;
}
@media screen and (orientation: portrait) {
  .RatesTable {
    width: 80vw;
    margin-left: 10vw;
  }
}
.RatesTable__row {
  width: 100%;
}
.RatesTable__row__cell {
  box-sizing: border-box;
  font-size: 1.8vh;
  text-align: left;
  font-weight: 500;
  height: 4vh;
  padding-left: 1vh;
  border-bottom: solid rgba(207, 207, 207, 0.575) 0.5vh;
}
.RatesTable__row #noborder {
  border: none;
}
.RatesTable__headrow {
  width: 100%;
  background-color: #37245c;
  color: white;
}
.RatesTable__headrow__cell {
  box-sizing: border-box;
  font-size: 1.8vh;
  text-align: left;
  font-weight: 500;
  height: 4vh;
  padding-left: 1vh;
  border-bottom: solid rgba(207, 207, 207, 0.575) 0.5vh;
}
.RatesTable__headrow__cell {
  border-radius: 2vh;
}
.RatesTable__headrow #th1 {
  border-top-right-radius: 0vh;
  border-bottom-right-radius: 0vh;
  border: none;
}
.RatesTable__headrow #th2 {
  border-top-left-radius: 0vh;
  border-bottom-left-radius: 0vh;
  border: none;
}