.ConvertButton {
  height: 6vh;
  width: 13vw;
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 1.5vh;
  background-color: #37245c;
  color: white;
  font-size: 2vh;

  transition: background-color 250ms;
  will-change: background-color;

  @media screen and (orientation: portrait) {
    width : 12vh;
  }
}

.ConvertButton:hover {
  background-color: #3e276d;
}
