.RatesTable {
  width: 50vw;
  height: auto;
  margin-left: 25vw;
  margin-top: 5vh;

  @media screen and (orientation : portrait){
    width : 80vw;
    margin-left : 10vw;
  }

  @mixin row() {
    width: 100%;

    &__cell {
      box-sizing: border-box;
      font-size: 1.8vh;
      text-align: left;
      font-weight: 500;
      height: 4vh;
      padding-left: 1vh;
      border-bottom: solid rgba(207, 207, 207, 0.575) 0.5vh;
    }
  }

  &__row {
    @include row();

    #noborder {
      border: none;
    }
  }

  &__headrow {
    @include row();
    background-color: #37245c;
    color: white;

    &__cell {
      border-radius: 2vh;
    }

    #th1 {
      border-top-right-radius: 0vh;
      border-bottom-right-radius: 0vh;
      border: none;
    }
    #th2 {
      border-top-left-radius: 0vh;
      border-bottom-left-radius: 0vh;
      border: none;
    }
  }
}
